import { Component } from "react";
import { setIdToken, setAccessToken } from "../utils/AuthService";

class Callback extends Component {
  constructor() {
    super();
    console.log("CALLBACK");
  }

  componentDidMount() {
    setAccessToken();
    setIdToken();
    window.location.href = "/";
  }

  render() {
    return null;
  }
}

export default Callback;



